/* src/App.css */
body {
  margin: 0;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.app {
  display: flex;
  height: 100vh;
  overflow: hidden;
}

.chat-list {
  width: 300px;
  border-right: 1px solid #e6e6e6;
  background-color: #f8f9fa;
  overflow-y: auto;
}

.chat-box {
  flex: 1;
  display: flex;
  flex-direction: column;
}

/* Rest of the existing styles... */

.app {
  display: flex;
  font-family: Arial, sans-serif; /* Set your preferred font family */
}

/* Reset some default styles */
body,
h1,
h2,
h3,
p,
ul,
li {
  margin: 0;
  padding: 0;
}

/* Apply some basic styling to the entire app */
.app {
  height: 100vh;
}

/* Add some styles to make the chat more visually appealing */
.chat-list {
  width: 20%;
  border-right: 1px solid #ccc;
  padding: 20px;
  overflow-y: auto;
  background-color: #f4f4f4;
}

.chat {
  padding: 10px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.chat:hover {
  background-color: #eee;
}

.chat-box {
  flex: 1;
  padding: 20px;
  background-color: #fff;
}

.chat-header {
  font-size: 18px;
  margin-bottom: 10px;
}

.chat-messages {
  max-height: 400px;
  overflow-y: auto;
}

.message {
  margin-bottom: 10px;
}

.message-input {
  display: flex;
  margin-top: 20px;
}

.message-input input {
  flex: 1;
  padding: 10px;
}

.message-input button {
  padding: 10px;
  margin-left: 10px;
  cursor: pointer;
  background-color: #4caf50;
  color: #fff;
  border: none;
}

.message-input button:hover {
  background-color: #45a049;
}
